import React, { useState } from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import EducationPopUpCard from './EducationPopUpCard';
import educationData from '../data/education';

const EducationCard = () => {
    const [openPopUp, setOpenPopUp] = useState(false);
    const [selectedEducation, setSelectedEducation] = useState(null);

    const handleOpenPopUp = (education) => {
        setSelectedEducation(education);
        setOpenPopUp(true);
    };

    const handleClosePopUp = () => {
        setOpenPopUp(false);
    };

    const handleOpenLink = (education) => {
        window.open(education.link, '_blank');
    }

    return (
        <Card sx={{ backgroundColor: 'primary.main', color: 'primary.contrastText', width: '100%', mb: 1, borderRadius: '16px' }}>
            <CardContent>
                <Typography variant="h5" mb={1}>Education</Typography>
                {educationData.map((education, index) => (
                    <div key={index}>
                        <Card sx={{ mb: 2 }}>
                            <CardContent style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', flexDirection: 'row' }}>
                                <Typography color='text.secondary' style={{ flexGrow: 1 }}>
                                    {education.degree && `${education.degree} -`} {education.school}
                                </Typography>
                                <Stack direction={'row'} alignItems='center' spacing={1}>
                                    {education.relevantCoursework.length > 0 && (
                                        <Button
                                            onClick={() => handleOpenPopUp(education)}
                                            variant="contained"
                                            color="primary"
                                            style={{
                                                color: "white",
                                                fontSize: '12px'
                                            }}
                                        >
                                            Relevant Courses
                                        </Button>
                                    )}
                                    <Button
                                        onClick={() => handleOpenLink(education)}
                                        variant="contained"
                                        color="primary"
                                        style={{
                                            color: "white",
                                            fontSize: '12px'
                                        }}
                                    >
                                        View Site
                                    </Button>
                                </Stack>
                            </CardContent>
                        </Card>
                    </div>
                ))}
                {selectedEducation && (
                    <EducationPopUpCard open={openPopUp} onClose={handleClosePopUp} education={selectedEducation} />
                )}
            </CardContent>
        </Card >
    );
};

export default EducationCard;
